body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --text-small: 0.85rem;
    --text-tiny: 0.70rem;
}

#content {
    max-width: 720px;
    margin: auto;
    padding: 0.75rem;
}

p {
    margin-bottom: 0.25rem;
}

.text-small {
    font-size: 0.85rem;
    font-size: var(--text-small);
}

.button-small {
    font-size: 0.85rem;
    font-size: var(--text-small);
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    padding-right: 0.5rem;
    padding-left: 0.5rem;
}

.help-icon {
    height: 0.8125rem;
    width: 0.8125rem;
    opacity: 0.7;
    vertical-align: top;
}

.help-icon:hover {
    opacity: 1;
}

#section-dropdown {
    padding: 0rem;
    color: #212529;
    color: var(--bs-body-color, #212529);
}
ul {
    list-style-type: none;
    padding: 0px;
}

li {
    margin-top: 0.3rem;
    margin-bottom: 0.3rem;
}

input {
    border: 1px solid #cccccc;
    border-radius: 0.25rem;
    padding: 0.25rem;
    font-size: 0.9rem;
}

.SubjectRow {
    display: flex; 
    justify-content: space-between;
    align-items: center;
}

.DeleteSubject {
    background: none;
    background-image: url(/atar-calculator/static/media/delete.86376276.svg);
    background-size: 1.25rem 1.25rem;

    height: 1.25rem;
    width: 1.25rem;
    min-width: 1.25rem;
    
    border: none;
    padding: 0px;
    margin-left: 1px;
    margin-right: 1px;
}

.DeleteSubject:hover {
    background-image: url(/atar-calculator/static/media/delete_filled.aeced149.svg);
}

.subject-selector__input-container {
    flex: 0 1!important;
    margin: 0px!important;
}

#save_img {
    height: 1.75rem;
    width: 1.75rem;
    opacity: 26%;
}

#save_img:hover {
    opacity: 54%;;
}
.fs-18px {
    font-size: 1.125rem;
    margin-bottom: 0rem;
}

.table-sm>:not(caption)>*>* {
    padding: 0.15rem 0.25rem;
}
